
















import Vue, {VueConstructor} from 'vue';
import PoweredBy from '@/configurator/components/PoweredBy.vue';
import ARButton from '@/configurator/components/top-bar/ARButton.vue';
import {hasArAssets, toBoolean} from '@/common/utils/helper';
import {UI_BUTTON} from '@/configurator/embedding/types';
import Layout from '@/common/components/Layout.vue';

export default (Vue as VueConstructor<Vue & {
  $refs: {
    viewerContainer: HTMLElement,
  },
}>).extend({

  components: {
    PoweredBy,
    ARButton,
    Layout,
  },

  props: {
    objectId: String,
  },

  data() {
    return {
      hasAssets: false,
    };
  },

  watch: {
    objectId: {
      immediate: true,
      async handler() {
        const id = this.objectId;
        if (id) {
          const rapiAccess = await this.$sdkConnector.getRapiAccess();
          const item = await rapiAccess.getItem(id);
          this.hasAssets = hasArAssets(item);
        }
      },
    },
  },

  computed: {
    showArButton(): boolean {
      return this.hasAssets && toBoolean(this.$store.state.uiState.initData.buttons?.[UI_BUTTON.AR]);
    },
  },

});
